@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  height: 100%;
  background: #e5e5e5;
}

.TodoTemplate{width:512px;margin-left:auto;margin-right:auto;margin-top:6rem;border-radius:4px;overflow:hidden}.TodoTemplate .app-title{background:#22b8cf;color:white;height:4rem;font-size:1.5rem;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.TodoTemplate .content{background:white}

.TodoInsert{display:-webkit-flex;display:flex;background:#495057}.TodoInsert input{background:none;outline:none;border:none;padding:0.5rem;font-size:1.125rem;line-height:1.5;color:white;-webkit-flex:1 1;flex:1 1}.TodoInsert input::-webkit-input-placeholder{color:#dee2e6}.TodoInsert input:-ms-input-placeholder{color:#dee2e6}.TodoInsert input::placeholder{color:#dee2e6}.TodoInsert button{background:none;outline:none;border:none;background:#868e96;color:white;padding-left:1rem;padding-right:1rem;font-size:1.5rem;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;cursor:pointer;transition:0.1s background ease-in}.TodoInsert button:hover{background:#abd5bd}

.TodoListItem{padding:1rem;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.TodoListItem:nth-child(even){background:#f8f9fa}.TodoListItem .checkbox{cursor:pointer;-webkit-flex:1 1;flex:1 1;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.TodoListItem .checkbox svg{font-size:1.5rem}.TodoListItem .checkbox .text{margin-left:0.5rem;-webkit-flex:1 1;flex:1 1}.TodoListItem .checkbox.checked svg{color:#22b8cf}.TodoListItem .checkbox.checked .text{color:#abd5bd;text-decoration:line-through}.TodoListItem .remove{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;font-size:#ff6b6b;cursor:pointer}.TodoListItem .remove:hover{color:#ff8787}.TodoListItem+.TodoListItem{border-top:1px solid #dee2e6}

.TodoList{min-height:320px;max-height:513px;overflow-y:auto}

